import DataTable from 'react-data-table-component';

function Section3(props) {
    const listProduct = props.listProduct;
    const newList = [];
    listProduct.forEach(item =>{
        delete item.img;
        delete item.info;
        delete item.link;

        switch (item.store) {
            case 'TZ':
                item.store = 'Topzone'
                break;
            case 'TGDD':
                item.store = 'Th gii di ng'
                break;
            case 'FPT':
                item.store = 'FPT'
                break;
            case 'HH':
                item.store = 'Hong H'
                break;
            case 'VT':
                item.store = 'Viettel'
                break;
            default:
                break;
        }

        item.price = item.price.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1.");
        newList.push(item);
    });
    const columns = [
        {
            name: 'Id',
            selector: row => row._id,
            sortable: true,
        },
        {
            name: 'Tn sn phm',
            selector: row => row.name,
            sortable: true,
        },
        {
            name: 'Gi sn phm',
            selector: row => row.price,
            sortable: true,
        },
        {
            name: 'Shop',
            selector: row => row.store,
            sortable: true,
        },
        {
            name: 'Danh mc',
            selector: row => row.category,
            sortable: true,
        },
    ];
    
    const data = newList;

    return (
        <section className="total-data">
            <p className='section-title'>Thng tin sn phm</p>
            <DataTable
                columns={columns}
                data={data}
                pagination
            />
        </section>
    );

}

export default Section3;
