import React from "react";

function LoginAdmin(props) {
    const textInput = React.useRef(null);

    React.useEffect(() => {
        textInput.current.focus();
    }, []);

    return ( 
        <div className="login-admin">
            <div className="login-admin_container">
                <p className="title"> ng nhp</p>
                { props.err ? <p className="err">Sai tn ng nhp hoc mt khu</p> : ''}
                <input className="input-basic" onFocus={props.getListData} id="userAdmin" placeholder="Tn ng nhp" ref={textInput} ></input>
                <input type="password" className="input-basic" id="passAdmin" placeholder="Mt khu"></input>
                <p className="btn-basic blue" onClick={props.handleSignIn}>ng nhp</p>
            </div>
        </div>
     );
}

export default LoginAdmin;